body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
  min-width: 600px;
  overflow: auto; }
  .app > * {
    padding-right: 17px; }
  .app__header {
    background-color: #282c34;
    height: 20vmin;
    width: 100vw;
    min-width: 600px;
    overflow: auto;
    display: table-cell;
    vertical-align: bottom;
    font-size: calc(10px + 2vmin);
    color: white; }
  .app__link-container {
    height: 1.7em; }
  .app__link {
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    padding-left: 1.2ch;
    padding-right: 1.2ch; }
    .app__link::before {
      content: "[ "; }
    .app__link::after {
      content: " ]"; }

.users-container__header-panel {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }
  .users-container__header-panel > * {
    margin: 30px; }

.switcher {
  display: inline-block;
  justify-content: flex-start;
  padding-top: 3px; }
  .switcher > * {
    margin: 0 8px 0 8px; }
  .switcher__button {
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
    cursor: pointer;
    opacity: 0.2; }
    .switcher__button:hover {
      opacity: 0.6; }
    .switcher__button_active {
      opacity: 1; }
      .switcher__button_active:hover {
        opacity: 1; }
    .switcher__button_type_tile {
      background-image: url(/static/media/tile-view.c0e4a0b3.svg); }
    .switcher__button_type_group {
      background-image: url(/static/media/group-view.f49be5eb.svg); }
    .switcher__button_type_table {
      background-image: url(/static/media/table-view.54f8aa47.svg); }

.search {
  display: inline-block;
  align-items: baseline; }
  .search__input, .search__label {
    font-size: calc(10px + 1vmin); }
  .search__label {
    padding-right: 1ch;
    color: #575757; }
  .search__input {
    outline: none;
    border: 2px solid lightgray;
    padding-left: 0.5ch;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; }

.users-table {
  display: table;
  width: 100%; }
  .users-table__cell {
    display: table-row;
    background-color: white; }
    .users-table__cell:nth-child(2n) {
      background-color: whitesmoke; }
    .users-table__cell_unmanaged {
      background-color: aliceblue; }
      .users-table__cell_unmanaged:nth-child(2n) {
        background-color: #dfe8f0; }

.user-groups {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 2rem;
  list-style: none;
  -webkit-padding-start: 40px;
          padding-inline-start: 40px;
  -webkit-padding-end: 40px;
          padding-inline-end: 40px; }
  .user-groups__group {
    display: flex;
    flex-direction: column;
    height: 300px;
    background-color: aliceblue; }

.user-tiles {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 2rem;
  list-style: none;
  -webkit-padding-start: 40px;
          padding-inline-start: 40px;
  -webkit-padding-end: 40px;
          padding-inline-end: 40px; }
  .user-tiles__tile {
    display: flex;
    flex-direction: column;
    height: 300px;
    background-color: bisque; }

